
#fbContainer{
    width: 100%;
    position: relative;
}
.crop-container{

    box-sizing: border-box;

    position: relative;

    margin: 0 auto;
}
.fbc-drag-overlay{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: transparent;
    z-index: 4;
}
.fbc-canvas{
    margin: 0 auto;
    box-sizing: border-box;
    width: 100%;
    outline: 4px solid #eee;
    position: relative;
    overflow: hidden;
    background: silver;
}
.fbc-canvas-img{
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0.2;
    max-width: none;
}
.cropbox,
.preview{
    position: absolute;
    width: 260px;
    height: 260px;
    overflow: hidden;
    z-index: 1;
}
.cropbox-image,
.preview img{
    position: absolute;
    max-width: none;
}
.cropbox {
    outline: 8px solid rgba(255, 255, 255, 0.55);
}
.slider{
    padding: 25px 0;
}
.file-selector{

    margin: 0 auto;
    width: 100px;
    height: 100px;
    border: 1px dashed #ddd;
    background-color: #f9f9f9;
    text-align: center;
}
.file-selector i{
    color: silver;
    font-size: 8rem;
}
.file-selector p{
    margin: 15px 0;
}
.fbc-overlay{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 40, 47, 0.45);
    z-index: 19;
}
.fbc-overlay i{
    font-size: 80px;
    color: #fff;
    position: relative;
    top: calc(50% - 40px);
    left: calc(50% - 40px);
}
input[type="range"] {
  -webkit-appearance: none;
  background-color: #bdc3c7;
  width: 100%;
  height: 5px;
  border-radius: 5px;
  margin: 0 auto;
  outline: 0;
}
input[type=range]:focus{
  outline: none;
}
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  background: white;
  border: 2px solid #1ECD97;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.fbc-footer{
    height: auto;
    margin: 0 auto;
    margin-top: 1rem;
}
.fbc-footer-left,
.fbc-footer-right{
    display: inline-block;
    margin-right: -4px;
    width: 50%;
    height: 30px;
    line-height: 30px;
}
.fbc-footer-right{
    text-align: right;
}
.fbc-output{
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    outline: 4px solid #eee;
    position: relative;
    overflow: hidden;
    background: silver;
}
.btn-crop {
    outline:none;
    height: 40px;
    text-align: center;
    min-width: 110px;
    border-radius:40px;
    background: #fff;
    border: 2px solid #1ECD97;
    color: #1ECD97;
    letter-spacing:1px;
    text-shadow:0;
    cursor: pointer;
    transition: all 0.25s ease;
}
.btn-crop:hover {
    color:white;
    background: #1ECD97;
}
.btn-crop:active {
    letter-spacing: 2px ;
}
.fbc-loader:after{
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    border: 4px solid #f3f3f3;
    border-radius: 50%;
    border-top: 4px solid #3498db;
    width: 25px;
    height: 25px;
    margin-left: -12.5px;
    margin-top: -12.5px;
    -webkit-animation: fbc-spin 2s linear infinite;
    animation: fbc-spin 2s linear infinite;
}
.fbc-loader.is-small:after{
    width: 40px;
    height: 40px;
    margin-left: -20px;
    margin-top: -20px;
}
.fbc-overlay.is-large:after{
    width: 60px;
    height: 60px;
    margin-left: -30px;
    margin-top: -30px;
}
@-webkit-keyframes fbc-spin{
0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
}
}
@keyframes fbc-spin{
0%{
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg)
}
}




